.blogPost {
  max-width: 1200px;
  margin: auto;
}

.blogPost h1 {
  margin-bottom: 2rem;
}

.blogPost h4 {
  margin-block: 2rem 0.5rem;
}

.blogPost .img-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.blogPost img {
  max-width: 500px;
  margin-block: 1rem;
  border: 2px solid var(--color-pink);
  border-radius: 10px;
}

@media screen and (max-width: 650px) {
  .blogPost img {
    max-width: 100%;
  }
}
