@import url("https://fonts.googleapis.com/css2?family=Courgette&family=Lato:wght@100;300;400;700;900&family=Roboto:wght@100;300;400;500;700;900&display=swap");

:root {
  --font-logo: "Courgette", cursive;
  --font-head: "Lato", sans-serif;
  --font-text: "Roboto", sans-serif;

  --color-pink: #ff6b6b;
  --color-l-pink: #ff7e7e;
  --color-background: #fffcfc;
  --color-black: #000;
  --color-white: #fff;
}

body {
  background: var(--color-background);
  overflow-x: hidden;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

a {
  text-decoration: none;
  color: unset;
}

ul {
  list-style: none;
}

::selection {
  background-color: var(--color-pink);
  color: black;
}

::-moz-selection {
  background-color: var(--color-pink);
  color: black;
}

.slide-bottom {
  -webkit-animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
  animation: slide-bottom 0.5s cubic-bezier(0.25, 0.46, 0.45, 0.94) both;
}

@-webkit-keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes slide-bottom {
  0% {
    -webkit-transform: translateY(-25%);
    transform: translateY(-25%);
  }
  100% {
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}
