.before,
.after {
  margin: 0;
}

.gallery {
  width: 90%;
  margin: auto;
}

.before figcaption,
.after figcaption {
  background: var(--color-pink);
  border: 1px solid var(--color-white);
  border-radius: 12px;
  color: var(--color-white);
  opacity: 0.8;
  padding: 10px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  line-height: 100%;
}

.before figcaption {
  left: 10px;
}

.after figcaption {
  right: 10px;
}

.gallery-container {
  display: grid;
  justify-content: center;
  align-items: flex-start;
  grid-template-columns: 1fr 1fr;
  margin-top: 2rem;
  gap: 2rem;
  max-width: 1200px;
}

.gallery-box {
  margin-top: 1rem;
}

.gallery-name {
  font-family: var(--font-head);
  color: var(--color-black);
  padding-top: 0.3rem;
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  transition: 0.5s ease;
}

.plus {
  margin-top: 4rem;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
}

@media screen and (max-width: 1150px) {
  .before figcaption,
  .after figcaption {
    padding: 6px;
    opacity: 0.7;
  }

  .gallery {
    width: 100%;
  }

  .before figcaption {
    left: 4px;
  }

  .after figcaption {
    right: 4px;
  }
}

@media screen and (max-width: 850px) {
  .gallery-container {
    grid-template-columns: 1fr;
  }
}

@media screen and (max-width: 650px) {
  .gallery-name {
    padding-top: 0;
    font-size: 17px;
  }
}

@media screen and (max-width: 450px) {
  .gallery-container {
    margin-top: 1rem;
  }
  .gallery-box {
    margin-top: 0.3rem;
  }
  .plus {
    margin-top: 2rem;
  }
}

@media screen and (max-width: 300px) {
}
