.pageheader {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  min-height: 70vh;
  width: 100%;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}

.pageheader-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(36, 30, 30, 0.7);
}

.pageheader-info {
  z-index: 1;
  width: 50%;
  margin-left: 5%;
}

.pageheader-info p {
  margin-bottom: 2rem;
}

.pageheader-info h2 {
  margin-top: 2rem;
  line-height: 1.2;
}

@media screen and (max-width: 1150px) {
  .pageheader {
    min-height: 40vh;
  }
}

@media screen and (max-width: 850px) {
  .pageheader-info {
    width: 100%;
    margin-left: initial;
  }
}
