.blog-post-content a {
  margin-top: 4rem;
}

.blog-post-content .plus {
  margin-top: 4rem;
}

.blog-posts {
  display: grid;
  justify-content: center;
  align-items: flex-start;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-top: 2rem;
  width: 90%;
}

.blog-post {
  min-height: 20vh;
  display: flex;
  justify-content: center;
  align-items: stretch;
  border: 2px solid var(--color-pink);
  border-radius: 20px;
  width: 100%;
  gap: 0.5rem;
  cursor: pointer;
  background-color: var(--color-white);
  transition: 0.6s ease;
}

.blog-post-img {
  width: 40%;
  border-right: 2px solid var(--color-pink);
  position: relative;
  overflow: hidden;
  border-radius: 20px 0 0 20px;
}

.blog-post-img img {
  position: absolute;
  width: 100%;
  height: 100%;
  transition: transform 0.6s ease;
  object-fit: cover;
  top: 0;
  left: 0;
}

.blog-post:hover .blog-post-img img {
  transform: scale(1.15);
}

.blog-post:hover {
  box-shadow: 0 0 10px rgb(255, 107, 107, 0.5);
}

.blog-post-content {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: start;
  padding-right: 2%;
}

.blog-post-content h4 {
  font-size: 25px;
  font-family: var(--font-head);
  padding-top: 5%;
}

.blog-post-content p {
  padding-block: 5%;
}

.blog-post-content a {
  padding-bottom: 5%;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
}

@media screen and (max-width: 1150px) {
  .blog-posts {
    width: 100%;
  }
  .blog-post-content h4 {
    font-size: 22px;
  }
  .blog-post-content a {
    display: none;
  }
}

@media screen and (max-width: 850px) {
  .blog-posts {
    grid-template-columns: 1fr;
    gap: 1rem;
    margin-top: 1rem;
  }
  .blog-post-content a {
    display: initial;
  }
}

@media screen and (max-width: 650px) {
  .blog-post-content h4 {
    font-size: 20px;
  }
  .blog-post {
    border-radius: 15px;
  }
  .blog-post-img {
    border-radius: 15px 0 0 15px;
  }
}

@media screen and (max-width: 450px) {
  .blog-post-content h4 {
    font-size: 18px;
  }
  .blog-post-content a {
    display: none;
  }
  .blog-post-content a {
    margin-top: 2rem;
  }
  .blog-post-content .plus {
    margin-top: 2rem;
  }
}

@media screen and (max-width: 300px) {
}
