.form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.form h2 {
  margin-bottom: 2rem;
}

.form form {
  width: 100%;
  max-width: 1000px;
}

.form-container {
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 1fr;
  margin-bottom: 1rem;
  gap: 1rem;
}

.input-container {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.input-container input {
  width: 100%;
  appearance: textfield;
  padding: 0.4rem 0.6rem;
  border-radius: 5px;
  border: 1px solid var(--color-pink);
  color: var(--color-black);
  outline: none;
}

.input-container input:focus {
  outline: none;
  background-color: #ffdfdf;
  box-shadow: 0 0 3px var(--color-l-pink);
}

.input-container textarea {
  width: 100%;
  padding: 0.4rem 0.6rem;
  border-radius: 5px;
  border: 1px solid var(--color-pink);
  color: var(--color-black);
  outline: none;
  resize: none;
}

.input-container textarea:focus {
  outline: none;
  background-color: #ffdfdf;
  box-shadow: 0 0 3px var(--color-l-pink);
}

.input-container button {
  width: 100%;
  margin-top: 1.5rem;
  padding: 0.4rem 0.6rem;
  border-radius: 5px;
  background-color: var(--color-pink);
  border: 1px solid var(--color-pink);
  color: var(--color-white);
  outline: none;
  cursor: pointer;
  transition: 0.3s ease;
}

.input-container button:hover {
  background-color: var(--color-l-pink);
}

.alert-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 2;
  object-fit: cover;
}

.alert {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 6;
  object-fit: cover;
}

.alert-content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  max-width: 600px;
  background-color: #faf3f3;
  text-align: center;
  gap: 0.5rem;
  z-index: 5;
  padding: 2rem 3rem;
  border: 3px solid var(--color-pink);
  border-radius: 20px;
}

/*==========Media Query==========*/

@media screen and (max-width: 850px) {
  .alert-content {
    width: 95%;
    max-width: 600px;
  }
}

@media screen and (max-width: 650px) {
  .form-container {
    grid-template-columns: 1fr;
    margin-bottom: 0.5rem;
    gap: 0.5rem;
  }
  .alert-content {
    width: 85%;
    padding: 2rem 2rem;
  }
}

@media screen and (max-width: 450px) {
  .alert-content {
    max-width: 300px;
    width: 85%;
    padding: 2rem 1rem;
  }
}
