.comments {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-wrap: wrap;
  gap: 3rem;
  margin-top: 2rem;
}

.comment-box {
  flex: 1;
  border: 2px solid var(--color-pink);
  border-radius: 40px;
  text-align: center;
  padding: 1.5rem;
  cursor: pointer;
  background-color: var(--color-white);
  min-width: 18rem;
  max-width: 25rem;
  transition: 0.3s ease;
}

.comment-box:hover {
  box-shadow: 0 0 8px rgb(255, 107, 107, 0.8);
  transform: scale(1.01);
}

.comment-box img {
  width: 50px;
}

.comment-name {
  font-size: 20px;
  font-family: var(--font-text);
  font-weight: 700;
  padding: 0.5rem;
}

.stars {
  padding-top: 1rem;
  font-size: 30px;
  color: var(--color-pink);
}

