@media screen and (max-width: 650px) {
  .comments {
    gap: 1rem;
    margin-top: 1rem;
    flex-direction: column;
  }
}

@media screen and (max-width: 450px) {
  .comment-box {
    border-radius: 35px;
    padding: 1rem;
    min-width: initial;
  }
}

