.section-padding {
  padding: 7rem 8rem 2rem;
}

.flex-center {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.app-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 90vh;
}

.app-wrapper-info {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
}

.app-wrapper-img {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-left: 1rem;
}

.app-wrapper-img-reverse {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;

  margin-right: 2rem;
}

.app-wrapper-img img {
  width: 100%;
}

.btn {
  background-color: var(--color-pink);
  color: var(--color-white);
  font-family: var(--font-head);
  font-weight: 700;
  letter-spacing: 0.04em;
  line-height: 28px;
  font-size: 16px;
  padding: 0.8rem 2rem;
  border-radius: 100px;
  border: 3px solid var(--color-pink);
  outline: none;
  cursor: pointer;
  transition: transform 0.4s ease;
  transition: 0.4s ease;
}

.btn:hover {
  background-color: var(--color-l-pink);
  border: 3px solid var(--color-l-pink);
  transform: translateY(-7px);
}

.f-text {
  font-family: var(--font-text);
  color: var(--color-black);
  font-weight: 400;
  letter-spacing: 0.04em;
  line-height: 1.4;
  font-size: 16px;
}

.f-header {
  font-family: var(--font-head);
  color: var(--color-black);
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 1.4;
  font-size: 55px;
}

.f-head {
  font-family: var(--font-head);
  color: var(--color-black);
  font-weight: 700;
  letter-spacing: 1px;
  line-height: 1.4;
  font-size: 45px;
  margin-bottom: 1rem;
  text-align: center;
}

.f-sub-head {
  font-family: var(--font-head);
  color: var(--color-pink);
  font-weight: 700;
  letter-spacing: 0.04em;
  line-height: 1;
  font-size: 23px;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
  .btn,
  .f-sub-head {
    font-size: 26px;
  }
  .f-text {
    font-size: 19px;
  }
  .f-header {
    font-size: 85px;
  }
  .f-head {
    font-size: 70px;
  }
  .section-padding {
    padding: 9rem 9rem 2rem;
  }
}

@media screen and (max-width: 1150px) {
  .app-wrapper {
    flex-direction: column;
  }
  .app-wrapper-reverse {
    flex-direction: column-reverse;
  }
  .app-wrapper-img {
    margin: 2rem 0 0 0;
  }
  .section-padding {
    padding: 7rem 5rem 2rem;
  }
}

@media screen and (max-width: 850px) {
  .section-padding {
    padding: 7rem 2rem 2rem;
  }
}

@media screen and (max-width: 650px) {
  .section-padding {
    padding: 7rem 1.5rem 2rem;
  }
  .f-sub-head {
    font-size: 20px;
  }
  .f-text {
    font-size: 14px;
  }
  .f-header {
    font-size: 40px;
  }
  .f-head {
    font-size: 30px;
  }
  .btn {
    font-size: 14px;
    padding: 0.5rem 1.8rem;
  }
}

@media screen and (max-width: 450px) {
  .section-padding {
    padding: 6rem 1rem 1rem;
  }
  .f-text {
    font-size: 13px;
  }
  .f-sub-head {
    font-size: 17px;
  }
  .f-header {
    font-size: 30px;
  }
  .f-head {
    font-size: 23px;
  }
}

@media screen and (max-width: 300px) {
  .section-padding {
    padding: 6rem 0.2rem 1rem;
  }
}
