.video {
  height: 90vh;
  position: relative;
  margin-block: 4rem 2rem;
}

.video video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video-overlay {
  position: absolute;
  inset: 0;
  background: rgba(32, 23, 23, 0.5);
}

.video-overlay-circle {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  border: 2px solid var(--color-pink);
  cursor: pointer;
}
