.messenger {
  position: fixed;
  bottom: 40px;
  right: 30px;
  width: 100px;
  height: 100px;
  cursor: pointer;
}

.messenger img {
  width: 100%;
}

@media screen and (max-width: 1150px) {
  .messenger {
    bottom: 40px;
    right: 30px;
    width: 80px;
    height: 80px;
  }
}

@media screen and (max-width: 650px) {
  .messenger {
    bottom: 25px;
    right: 15px;
  }
}

@media screen and (max-width: 450px) {
  .messenger {
    width: 60px;
    height: 60px;
  }
}
