.form-close-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgb(0, 0, 0, 0.5);
  z-index: 2;
  object-fit: cover;
}

.form-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;

  background-color: rgb(0, 0, 0, 0.5);
  z-index: 6;
  object-fit: cover;
}

.form-component {
  width: 100%;
  max-width: 1000px;
  background-color: var(--color-white);
  z-index: 5;
  padding: 2rem 3rem;
  border: 1px solid var(--color-pink);
  border-radius: 20px;
  margin-inline: 5%;
}

@media screen and (max-width: 450px) {
  .form-component {
    padding: 1.5rem 0.5rem;
  }
}
