.navbar {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 5;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 6rem;
  background-color: var(--color-background);
  box-shadow: 0 0 5px rgb(255, 107, 107, 0.2);
}

.navbar-logo {
  width: 140px;
  cursor: pointer;
}

.navbar-logo img {
  width: 100%;
}

.navbar-links ul {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
}

.navbar-links ul li {
  padding: 0 1.3rem;
  font-weight: 500;
  font-family: var(--font-text);
  color: var(--color-black);
  font-size: 16px;
}

.navbar-links ul li:last-child {
  padding-right: 0;
}

.navbar-links ul li:hover {
  color: var(--color-pink);
}

.active {
  color: var(--color-pink);
}

.navbar-phones {
  display: none;
  cursor: pointer;
}

.navbar-phone-overlay {
  position: fixed;
  width: 100%;
  height: 100vh;
  top: 0;
  right: 0;
  background: var(--color-pink);
  transition: 0.5s ease;

  z-index: 5;
  text-align: center;
}

.navbar-phone-overlay ul li {
  margin: 1.5rem 0;
  font-weight: 500;
  font-family: var(--font-text);
  color: var(--color-white);
  font-size: 16px;
}

.navbar-phone-overlay ul li:hover {
  color: var(--color-black);
}

.navbar-overlay-close {
  position: absolute;
  cursor: pointer;
  width: 32px;
  height: 32px;
}

.navbar-logo-white {
  position: absolute;
  top: 1rem;
  left: 1.1rem;
  cursor: pointer;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
  .navbar-logo {
    width: 200px;
  }
}

@media screen and (max-width: 1150px) {
  .navbar-links {
    display: none;
  }
  .navbar-phones {
    display: initial;
  }
  .active {
    color: var(--color-black);
  }
}

@media screen and (max-width: 850px) {
  .navbar {
    padding: 0.5rem 2rem 0.3rem;
  }
  .navbar-overlay-close {
    top: 1rem;
    right: 2rem;
  }
}

@media screen and (max-width: 650px) {
  .navbar {
    padding: 0.5rem 5% 0.3rem;
  }
  .navbar-overlay-close {
    top: 1rem;
    right: 5%;
  }
}

@media screen and (max-width: 450px) {
  .navbar-logo {
    width: 110px;
  }
}

