.contact {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1rem;
}
.contact-img {
  border-radius: 0 40px 40px 0;
  height: 80vh;
  margin-right: 3rem;
  margin-left: 1rem;
}

.contact-img img {
  border-radius: 0 40px 40px 0;
  width: 100%;
  height: 100%;
}

.visite-us {
  gap: 5%;
}

.app-wrapper-info h2 {
  text-align: start;
}

.visite-info {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: start;
  margin-top: 2rem;
  gap: 1.8rem;
}

.visite {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: start;
  flex-direction: row;
  gap: 0.4rem;
}

.visite-icons {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: start;
}

.visite ul {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  gap: 0.4rem;
}

.visite-icon {
  color: var(--color-pink);
  font-size: 2rem;
  cursor: pointer;
  transition: 0.4s ease;
}

.visite-icon:hover {
  margin-right: 0.5rem;
  transform: scale(1.1);
}

.map {
  width: 85%;
  height: 45vh;
}

.map iframe {
  width: 100%;
  height: 100%;
  border: 2px solid var(--color-pink);
  border-radius: 25px;
  object-fit: cover;
  top: 0;
  left: 0;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
}

@media screen and (max-width: 1150px) {
  .contact-img {
    border-radius: 25px;
    margin: 2rem 0 0 0;
  }

  .contact-img img {
    border-radius: 25px;
  }

  .map {
    width: 100%;
    height: 30vh;
  }

  .visite-info {
    margin-top: 0;
  }
}

@media screen and (max-width: 850px) {
  .contact {
    gap: 2rem;
  }
  .visite-us {
    gap: initial;
  }
}

@media screen and (max-width: 650px) {
  .contact-img {
    width: 100%;
    height: 100%;
  }
}

@media screen and (max-width: 450px) {
  .visite-icon {
    font-size: 1.8rem;
  }
}

@media screen and (max-width: 300px) {
}
