.services-box {
  display: grid;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  margin-top: 2rem;
  grid-template-columns: 1fr 1fr 1fr 1fr;
}

.service-box {
  background-color: var(--color-white);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 1.5rem;
  border: 3px solid var(--color-pink);
  border-radius: 60px;
  min-width: 14rem;
  height: 16rem;
  transition: transform 0.5s ease;
  transition: 0.5s ease;
  cursor: pointer;
}

.service-box:hover {
  transform: translateY(-8px);
  background-color: var(--color-pink);
}

.service-box:hover .service-name {
  color: var(--color-white);
}

.service-box:hover .service-img img {
  filter: invert(100%);
}

.service-img {
  width: 35%;
}

.service-box img {
  width: 100%;
  transition: 0.5s ease;
}

.service-name {
  font-family: var(--font-head);
  color: var(--color-black);
  font-weight: 700;
  font-size: 20px;
  text-align: center;
  transition: 0.5s ease;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
  .service-box {
    min-height: 16rem;
  }
}

@media screen and (max-width: 1150px) {
  .services-box {
    grid-template-columns: 1fr 1fr;
  }
  .service-box {
    max-width: 20rem;
  }
}

@media screen and (max-width: 850px) {
}

@media screen and (max-width: 650px) {
  .services-box {
    gap: 1rem;
  }
  .service-box {
    min-width: 12rem;
    height: 14rem;
    border-radius: 50px;
    border: 2.5px solid var(--color-pink);
  }
  .service-name {
    font-size: 17px;
  }
  .service-img {
    width: 40%;
  }
}

@media screen and (max-width: 450px) {
  .services-box {
    gap: 0.5rem;
    margin-top: 1rem;
  }
  .service-box {
    min-width: 45%;
    max-width: 18rem;
    max-height: 20vh;
    border-radius: 35px;
    gap: 1rem;
    border: 2.2px solid var(--color-pink);
  }
  .service-box:hover {
    transform: translateY(0px);
    background-color: var(--color-pink);
  }
}

@media screen and (max-width: 300px) {
  .services-box {
    grid-template-columns: 1fr;
  }
}
