.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.footer-info {
  flex: 1;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding: 4rem 6rem;
}

.footer-contact,
.footer-sm,
.footer-links {
  flex: 1;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 8px;
}

.footer-contact p,
a {
  text-align: center;
}

.footer-sm p {
  cursor: pointer;
}

.footer-sm .btn {
  padding: 0.6rem 2rem;
}

.footer-contact-infos {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: start;
  margin-top: 1rem;
  gap: 1rem;
}

.footer-contact-info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: start;
  flex-direction: row;
  gap: 0.4rem;
}

.footer-contact-icons {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  text-align: start;
}

.footer-contact-icon {
  color: var(--color-pink);
  font-size: 2rem;
  cursor: pointer;
  transition: 0.4s ease;
}

.footer-contact-icon:hover {
  margin-right: 0.5rem;
  transform: scale(1.1);
}

.footer-sm-link-icon {
  font-size: 2.8rem;
  padding: 0.5rem;
  transition: transform 0.3s ease;
}

.footer-sm-link-icon:hover {
  color: var(--color-pink);
  transform: scale(1.2);
}

.footer-link ul li a {
  font-weight: 500;
}

.footer-link ul li {
  margin-block: 8px;
}

.footer-link ul li a:hover {
  font-weight: 700;
}

.footer-contact-info a {
  font-weight: 500;
}

.footer-contact-info a:hover {
  font-weight: 700;
}

.footer-copyrights {
  background-color: var(--color-pink);
  color: var(--color-white);
  font-family: var(--font-text);
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 0.4rem 6rem;
  font-weight: 500;
  font-size: 0.9rem;
  gap: 8px;
  border-top: 1px solid black;
}
.footer-copyrights a {
  text-align: center;
}

/*==========Media Query==========*/

@media screen and (min-width: 2000px) {
  .footer-copyrights {
    font-size: 30px;
  }
}

@media screen and (max-width: 1150px) {
  .footer-contact-infos {
    margin-top: 0;
  }
}

@media screen and (max-width: 850px) {
  .footer-info {
    flex-direction: column;
    gap: 2.5rem;
  }
  .footer-contact,
  .footer-sm,
  .footer-links {
    margin: 0;
  }
  .footer-info {
    padding: 4rem 2rem;
  }
  .footer-copyrights {
    padding: 0.4rem 2rem;
  }
}

@media screen and (max-width: 650px) {
  .footer-info {
    padding: 4rem 1.5rem 2rem;
  }
  .footer-copyrights {
    padding: 0.4rem 1.5rem;
    font-size: 14px;
    flex-direction: column;
  }
}

@media screen and (max-width: 450px) {
  .footer-contact-icon {
    font-size: 1.8rem;
  }
  .footer-info {
    padding: 3rem 0.5rem;
  }
  .footer-sm-link-icon {
    font-size: 2.5rem;
    padding: 0.5rem;
    transition: transform 0.3s ease;
  }
  .footer-copyrights {
    font-size: 13px;
  }
}

@media screen and (max-width: 300px) {
  .footer-info {
    padding: 3rem 0.2rem;
  }
}
