.about .app-wrapper-img img {
  width: 90%;
}

.doctor-name {
  color: var(--color-pink);
}

.about-features {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  text-align: start;
  margin-top: 2rem;
  gap: 1rem;
}

.about-feature {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  text-align: start;
  gap: 0.3rem;
}

.features-check {
  color: var(--color-pink);
  font-size: 2rem;
  cursor: pointer;
  transition: 0.4s ease;
}

.features-check:hover {
  margin-right: 0.5rem;
  transform: scale(1.1);
}

